import React, { useState } from 'react'
import Calender from '../calendar/Calender'
import moment from 'moment';
import AttendanceStatusCount from '../attendanceDashboard/AttendanceStatusCount';
import BackIcon from '../commanComponent/BackIcon';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

const EmpAttendanceCalender = () => {
    const [currentDate, setCurrentDate] = useState(moment());
    const {selectedEmployeeData}=useSelector(state =>state?.employee)
    return (
        <div>
            <div className='cardBox'>
                <div className='d-flex mb-3'>
                    <div>
                        <Link to={"/team-attendance"}>
                            <BackIcon />
                        </Link>
                    </div>
                    <div>
                        <h5 >
                            {selectedEmployeeData?.name}
                        </h5>
                    </div>
                    <div className='ms-2 mt-1'>
                        <span className='span-label'>Emp.ID:{selectedEmployeeData?.emp_id}</span>
                    </div>
                </div>
                <AttendanceStatusCount />
                <Calender
                    setCurrentDate={setCurrentDate}
                    currentDate={currentDate}
                    attendanceDetails={''}
                    handleOpenClose={''}
                // setSelectedCalenderData={setSelectedCalenderData}
                />
            </div>
        </div>
    )
}

export default EmpAttendanceCalender
